import cuPanel from '@/components/custom/cu-panel.vue';
import cuCell from '@/components/custom/cu-cell.vue';
import cuCellGroup from '@/components/custom/cu-cell-group.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import { post, tokenpost } from '@/api/user.js';
import cuLoading from '@/components/custom/cu-loading.vue';
import { api } from '@/config/common.js';
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup,
		uniList,
		uniListItem
	},
	data() {
		return {
			reqData: {
				printertype: 0,
				printerid: 0,
				printername: '',
				printermodel: '',
				printercode: '',
				printerkey: ''
			},
			printTypeindex: 1,
			printModelIndex: 1,
			printList: [{ name: '' }, { name: '盒子' }, { name: '映美' }],
			printModel: [
				{ name: '' },
				{ name: 'Jolimark CFP-535W' },
				{ name: 'Aisino TY-805E' },
				{ name: 'Aisino XY-8010+' },
				{ name: 'Aisino AX-380+' },
				{ name: 'Epson LQ-1600K' },
				{ name: 'EPSON LQ-730K ESC/P2' },
				{ name: 'HP LaserJet Pro MFP M127-M128 PCLmS' },
				{ name: 'HP Color LaserJet 2800 Series PS' },
				{ name: 'HP LaserJet CP 1025' }
			],
			loading: false,
			title: '打印机设置'
		};
	},
	onShow() {},
	onLoad(option) {
		uni.$on('printModelChange', val => {
			console.log(val.value);
			this.reqData.printermodel = val.value;
		});
		this.loadData();
	},
	methods: {
		handlePrintChanage(val) {
			this.reqData.printertype = val.detail.value;
		},
		handlePrintModel(){
			uni.navigateTo({
				url:'/pages/my/print/printlist'
			})
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			});
		},
		
		loadData() {
			this.$refs.loading.open();
			const sendData = {};
			tokenpost(api.GetFirstActivePrinter, sendData)
				.then(res => {
					this.$refs.loading.close();
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.reqData = res.data.data;
					} else {
						//this.$api.msg(res.data.returnMessage)
					}
				})
				.catch(error => {
					this.$refs.loading.close();
					this.$api.msg('请求失败fail');
				});
		},
		Unbind() {
			this.$refs.loading.open();
			const sendData = {
				printerid: this.reqData.printerid
			};
			tokenpost(api.UnBindPrint, sendData)
				.then(res => {
					this.$refs.loading.close();
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.reqData.printerid = 0;
					} else {
						this.$api.msg(res.data.returnMessage);
					}
				})
				.catch(error => {
					this.$refs.loading.close();
					this.$api.msg('请求失败fail');
				});
		},
		arrayLookup(data, key, value) {
			var targetValue = '';
			for (var i = 0; i < data.length; i++) {
				if (data[i][key] == value) {
					targetValue = i;
					break;
				}
			}
			return targetValue;
		},
		handleSubmit() {
			const reqData = {
				printertype: this.reqData.printertype,
				printername: this.reqData.printername,
				printermodel: this.reqData.printermodel,
				printercode: this.reqData.printercode,
				printerkey: this.reqData.printerkey
			};

			if (reqData.printername.length == 0) {
				this.$api.msg('打印机名称不能为空');
				return;
			}
			if (reqData.printermodel.length == 0) {
				this.$api.msg('打印机型号不能为空！');
				return;
			}
			if (reqData.printertype == 0) {
				this.$api.msg('设备类型不能为空！');
				return;
			}
			if (reqData.printercode.length == 0) {
				this.$api.msg('设备编号不能为空');
				return;
			}
			if (reqData.printerkey.length == 0) {
				this.$api.msg('设备密钥不能为空！');
				return;
			}

			if (this.reqData.printerid != 0) {
				this.Unbind();
			}
			this.loading = true;
			tokenpost(api.BindPrint, reqData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.$api.msg('打印机设置成功');
						this.handleNavbarClickLeft()
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		}
	}
};
